.MainPage {
    background-color: aliceblue;
    /*height: 55vh;*/
    display: flex;
    margin-top: 100px;
    margin-bottom: 100px;
    min-width: 85%;
    max-width: 85%;
}

.sideBar {
    width: 20%;
    background-color: rgb(184 245 213 / 54%);
    padding: 23px;
    border: 1px solid #fff;
}

.packList {
    width: 80%;
    position: relative;
    border: 1px solid #fff;
}

.allPacks {
    min-height: 500px;
    max-height: 500px;
    margin: 10px;
    padding: 10px;
    overflow: auto;
    border: 1px solid #fff;
}

.packListHeader {
    width: 100%;
    text-align: center;
    padding: 10px;
    font-size: larger;
}

.searchAndAdd {
    padding: 10px;
    margin: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.profileDiv {
    display: flex;
    height: 100px;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 10px;
    flex-direction: column;
    margin-top: 20px;
}

.editBtn {
    height: 26px;
    width: 140px;
    margin: 12px;
}

.avatarImg {
    border-radius: 50%;
    width: 190px;
}

.doubleRange {
    margin-top: 20px;
    font-size: 12px;
}

.avatar {
    display: flex;
    justify-content: center;
    align-items: center;
}